* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.4rem;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  color: gray;
  width: 100%;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 2px solid gray;
  border-radius: 8px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 120px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  border-radius: 15px;
}